orders, $direction), size);\r\n      $sidebar-width: $sidebar-width - $size;\r\n    }\r\n    width: $sidebar-width;\r\n  }\r\n}\r\n","//\r\n// Base styles\r\n//\r\n\r\n.switch {\r\n  display: inline-block;\r\n  width: $switch-width;\r\n  height: $switch-height;\r\n}\r\n\r\n.switch-input {\r\n  display: none;\r\n}\r\n\r\n.switch-slider {\r\n  position: relative;\r\n  display: block;\r\n  height: inherit;\r\n  cursor: pointer;\r\n  background-color: $switch-bg;\r\n  border: $border-width solid $border-color;\r\n  transition: .15s ease-out;\r\n  @include border-radius($border-radius);\r\n\r\n  &::before {\r\n    position: absolute;\r\n    top: $switch-handle-margin;\r\n    left: $switch-handle-margin;\r\n    box-sizing: border-box;\r\n    width: $switch-height - ($switch-handle-margin * 2) - ($border-width * 2);\r\n    height: $switch-height - ($switch-handle-margin * 2) - ($border-width * 2);\r\n    content: \"\";\r\n    background-color: $switch-handle-bg;\r\n    border: 1px solid $border-color;\r\n    transition: .15s ease-out;\r\n    @include border-radius($border-radius / 2);\r\n  }\r\n}\r\n\r\n.switch-input:checked ~ .switch-slider {\r\n  &::before {\r\n    transform: translateX($switch-width - $switch-height);\r\n  }\r\n}\r\n\r\n.switch-input:disabled ~ .switch-slider {\r\n  cursor: not-allowed;\r\n  opacity: .5;\r\n}\r\n\r\n//\r\n// Switch Sizes\r\n//\r\n\r\n.switch-lg {\r\n  @include switch-size($switch-lg-width, $switch-lg-height, $switch-lg-font-size, $switch-handle-margin);\r\n}\r\n\r\n.switch-sm {\r\n  @include switch-size($switch-sm-width, $switch-sm-height, $switch-sm-font-size, $switch-handle-margin);\r\n}\r\n\r\n//\r\n// Switch with label\r\n//\r\n.switch-label {\r\n  width: $switch-label-width;\r\n\r\n  .switch-slider {\r\n    &::before {\r\n      z-index: 2;\r\n    }\r\n    &::after {\r\n      position: absolute;\r\n      top: 50%;\r\n      right: 1px;\r\n      z-index: 1;\r\n      width: 50%;\r\n      margin-top: -.5em;\r\n      font-size: $switch-f